






























































.content {
  .statusMessage {
    &__header {
      font-size: 16px;
      &__header {
        font-family: 'ComfortaaBold', cursive;
        font-size: 18px;
      }
    }
  }
  .game {
    font-size: 2.25em;
    margin-top: 0.25em;
    margin-right: 1em;
    margin-left: 1em;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;

    &__container {
      align-self: center;

      .grid {
        margin-top: -1em;
        transform: rotateX(42deg) rotateZ(25deg) translateZ(-1.2em);
        border-collapse: collapse;

        &:nth-child(2) {
          margin-top: -1.5em;
          transform: rotateX(42deg) rotateZ(25deg) translateZ(-0.7em);
        }

        &:nth-child(3) {
          transform: rotateX(42deg) rotateZ(25deg) translateZ(0px);
        }

        &:nth-child(4) {
          transform: rotateX(42deg) rotateZ(25deg) translateZ(0px);
          margin-top: -1.75em;
        }

        .row {
          .col {
            width: 60px;
            height: 30px;
            border: 2pt solid #000;
            border-radius: 5px;
            margin: 0.05em;
            background-color: white;
            padding: 0;
            transform: perspective(1px) translateZ(0);
            box-shadow: 0 0 1px rgba(0, 0, 0, 0);
            transition-duration: 0.3s;
            transition-property: transform;
            cursor : pointer;
            a {
              display: block;
              height: 100%;
              width: 100%;
            }
            &:hover:not(.col__player1):not(.col__player2)  {
              background: #6c757d;
              transform: translateY(-10px);
            }
            &__player1 {
              background-color: red;
              cursor: default;
            }
            &__player2 {
              background-color: royalblue;
              cursor: default;
            }
            &:hover:before {
              opacity: 1;
              transform: translateY(5px);
            }
            &:before {
              pointer-events: none;
              position: absolute;
              z-index: -1;
              content: '';
              top: 100%;
              left: 5%;
              height: 10px;
              width: 90%;
              opacity: 0;
              background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0) 80%);
              transition-duration: 0.3s;
              transition-property: transform, opacity;
            }
          }
        }
      }
    }
  }
}
